import { monthTime } from "@/utils/formatTime.js";

const data = [
  {
    num: "13111111111",
    pwd: "ZR1998zr1123",
    time: "2020-11-07"
  }
];

const validVip = function({ num, pwd }) {
  let result = false;
  data.forEach(_v => {
    if (monthTime(_v.time) && _v.num === num && _v.pwd === pwd) {
      result = true;
    } else {
      result = false;
    }
  });
  return result;
};

export default validVip;
