<template>
  <el-dialog
    :visible.sync="show"
    width="400px"
    class="loginDialog"
    :close-on-click-modal="false"
    :before-close="handleBeforeClose"
  >
    <div slot="title" class="loginDialog-header">
      <!-- <img src="@/assets/images/logo2.png" alt="" class="loginDialog-logo"> -->
      <p class="loginDialog-title">
        登录斗技盒子
      </p>
    </div>
    <el-form
      :model="passwordForm"
      ref="passwordForm"
      :rules="pwdRules"
      class="dialog-Form"
    >
      <el-form-item prop="accountNumber">
        <el-input
          v-model="passwordForm.accountNumber"
          placeholder="手机号/账号"
          type="text"
          class="dialog-input"
          @focus="handleAccountFocus"
          @blur="handleAccountBlur"
        ></el-input>
      </el-form-item>
      <el-form-item prop="password">
        <el-input
          v-model="passwordForm.password"
          placeholder="输入密码"
          type="password"
          class="dialog-input"
          @focus="handlePwdFocus"
          @blur="handlePwdBlur"
        ></el-input>
      </el-form-item>
      <el-form-item>
        <el-button
          class="dialog-button--login"
          type="success"
          @click="handleLogin"
          >登录</el-button
        >
      </el-form-item>
    </el-form>
    <img
      src="@/assets/images/normal.png"
      alt=""
      class="loginDialog-img--normal"
      v-if="!pwdFocus && !accountFocus"
    />
    <img
      src="@/assets/images/mima.png"
      alt=""
      class="loginDialog-img--pwd"
      v-if="pwdFocus"
    />
    <img
      src="@/assets/images/zhanghao.png"
      alt=""
      class="loginDialog-img--account"
      v-if="accountFocus"
    />
    <p class="loginDialog-tips">注册账号请联系作者</p>
  </el-dialog>
</template>

<script>
import validVip from "@/data/vip.js";
import Cookies from "js-cookie";

export default {
  name: "Yys-Login",
  data() {
    return {
      show: false,
      passwordForm: {
        accountNumber: "",
        password: ""
      },
      pwdRules: {
        accountNumber: [
          { required: true, message: "请输入账号", trigger: "blur" }
        ],
        password: [
          { required: true, message: "请输入密码", trigger: "blur" },
          { min: 6, max: 16, message: "请输入6-16位密码" }
        ]
      },
      accountFocus: false,
      pwdFocus: false
    };
  },
  created() {},
  methods: {
    handleLogin() {
      this.$refs.passwordForm.validate(valid => {
        if (!valid) {
          return;
        }
        const params = {
          num: this.passwordForm.accountNumber,
          pwd: this.passwordForm.password
        };
        let result = validVip(params);
        if (!result) {
          this.$message.error("账号密码错误, 或Vip时效过期！");
          return;
        }
        const en = window.btoa(JSON.stringify(params));
        Cookies.set("doujibox_en", en, { expires: 7 });
        window.location.reload();
      });
    },
    handleBeforeClose() {
      this.$confirm("确认关闭？")
        .then(() => {
          this.show = false;
        })
        .catch(() => {});
    },
    handleAccountFocus() {
      this.accountFocus = true;
    },
    handleAccountBlur() {
      this.accountFocus = false;
    },
    handlePwdFocus() {
      this.pwdFocus = true;
    },
    handlePwdBlur() {
      this.pwdFocus = false;
    }
  }
};
</script>

