import YysLogin from "./YysLogin.vue";
let instance = null;

export default {
  install(Vue) {
    if (!instance) {
      let Components = Vue.extend(YysLogin);
      instance = new Components({
        el: document.createElement("div")
      });
      document.body.appendChild(instance.$el);
    }

    instance.show = false;
    let loginMethods = {
      show() {
        instance.show = true;
      },
      hide() {
        instance.show = false;
      }
    };
    Vue.$YysLogin = loginMethods;
    Vue.prototype.$YysLogin = Vue.$YysLogin;
  }
};
