import Vue from "vue";
import VueRouter from "vue-router";
import routes from "./route.js";
import Nprogress from "nprogress";
import "nprogress/nprogress.css";
import Cookies from "js-cookie";
import validVip from "@/data/vip.js";
import { Message } from "element-ui";
import Store from "@/store/index";
Vue.use(VueRouter);

const router = new VueRouter({
  // mode: "history",
  base: process.env.BASE_URL,
  routes
});

router.firstInit = false;

router.beforeEach((to, from, next) => {
  Nprogress.start();
  const Token = Cookies.get("doujibox_en");
  if (!router.firstInit && Token) {
    const en = window.atob(Token);
    let params = "";
    try {
      params = JSON.parse(en);
    } catch {
      Message.error("登录失效，请重新登录");
      Cookies.remove("doujibox_en", { path: "", domain: "" });
      window.location.reload();
      return;
    }
    if (!validVip(params)) {
      Message.error("登录失效，请重新登录");
      Cookies.remove("doujibox_en", { path: "", domain: "" });
      window.location.reload();
      return;
    }
    Store.commit("setVip", true);
    Store.commit("setNum", params.num);
  }
  next();
});
// eslint-disable-next-line
router.afterEach((to, from) =>{
  Nprogress.done();
});
export default router;
