import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    vip: false,
    num: null
  },
  mutations: {
    setVip(state, val) {
      state.vip = val;
    },
    setNum(state, val) {
      state.num = val;
    }
  },
  actions: {},
  modules: {}
});
