                      :class="route.meta.nav.icon"
                    ></i>
                    <span>{{ route.meta.nav.title }}</span>
                  </template>

                  <template v-for="item in route.children">
                    <el-menu-item
                      v-if="!haveChildren(item)"
                      :key="item.name"
                      :index="item.name"
                      :route="{ name: item.name }"
                    >
                      <!-- <i
                        v-if="item.meta.nav.icon"
                        :class="item.meta.nav.icon"
                      ></i> -->
                      <span class="fa-dot"></span>
                      <span>{{ item.meta.nav.title }}</span>
                    </el-menu-item>
                    <el-submenu
                      class="el-submenu-level_2"
                      v-else
                      :key="item.name"
                      :index="item.name"
                    >
                      <template slot="title">
                        <!-- <i
                          v-if="item.meta.nav.icon"
                          :class="item.meta.nav.icon"
                        ></i> -->
                        <span class="fa-diamond"></span>
                        <span>{{ item.meta.nav.title }}</span>
                      </template>
                      <el-menu-item
                        v-for="data in item.children"
                        :key="data.name"
                        :index="data.name"
                        :route="{ name: data.name }"
                      >
                        <!-- <i
                          v-if="data.meta.nav.icon"
                          :class="data.meta.nav.icon"
                        ></i> -->
                        <span class="fa-dot"></span>
                        <span>{{ data.meta.nav.title }}</span>
                      </el-menu-item>
                    </el-submenu>
                  </template>
                </el-submenu>
              </template>
            </el-menu>
          </el-scrollbar>
        </div>
      </div>
    </div>
    <div class="yys-content-container">
      <div class="yys-content-header">
        <div class="yys-content-header-left">
          <div class="collapse-icon" @click="handleCollapse">
            <i :class="[collapse ? 'el-icon-s-unfold' : 'el-icon-s-fold']"></i>
          </div>
        </div>
        <div class="yys-content-header-right">
          <p class="yys-login-button" v-if="!vip" @click="handleShowLogin">
            登录
          </p>
          <p class="yys-user-info" v-else>尊敬的 {{ num }} 用户</p>
        </div>
      </div>
      <div class="yys-content-body">
        <router-view />
        <el-backtop target=".yys-content-body" :bottom="100"></el-backtop>
