</template>

<script>
import routes from "@/router/route";
import { mapState } from "vuex";

export default {
  data() {
    return {
      backgroundColor: "#fff",
      textColor: "#666",
      activeTextColor: "#fff",
      defaultActive: this.$route.name,
      menuRoutes: [],
      collapse: false,
      nickName: "",
      isLogin: false
    };
  },
  computed: {
    ...mapState(["vip", "num"]),
    siderWidth() {
      return this.collapse ? "64px" : "227px";
    }
  },
  created() {
    this.getRoutes();
  },
  methods: {
    getRoutes() {
      this.menuRoutes = this.filterRoutes(routes);
    },
    filterRoutes(routes) {
      let result = [];
      routes.forEach(data => {
        if (data.meta && data.meta.nav) {
          let item = {
            name: data.name,
            meta: data.meta
          };
          if (data.children) {
            item.children = this.filterRoutes(data.children);
          }
          result.push(item);
        } else if (data.children) {
          this.filterRoutes(data.children).forEach(item => {
            result.push(item);
          });
        }
      });
      return result;
    },
    handleShowLogin() {
      this.$YysLogin.show();
    },
    haveChildren(route) {
      let children = route.children || [];
      return children.length;
    },
    handleCollapse() {
      this.collapse = !this.collapse;
    },
    handleCommand(command) {
      this[command]();
    }
  }
};
</script>

