const shishen = function(key) {
  if (!Array.isArray(key)) {
    return [];
  }
  let result = [];
  key.forEach(_s => {
    let temp = data.find(arr => _s === arr.name);
    result.push(temp);
  });
  return result;
};

const Arr = function(data) {
  data.map(item => {
    item.blue = shishen(item.blue);
    item.red = shishen(item.red);
    return item;
  });
  return data;
};
export default Arr;
